@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');
@import url("@fontsource/public-sans");


.backgroundContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 500px;
    height: 100%;
    background-color: rgb(248, 250, 252);
    max-width: 650px;
}

.successshow {
    width: 100%;
    border: 1px solid #e5e7eb;
    border-radius: .375rem;
    font-size: .875rem;
    line-height: 21px;
    margin-bottom: .75rem;
    margin-top: .75rem;
    --tw-text-opacity: 1;
    color: rgb(36 151 130 / var(--tw-text-opacity));
    --tw-bg-opacity: 1;
    background-color: rgb(234 250 247 / var(--tw-bg-opacity));
    --tw-border-opacity: 1;
    border-color: rgb(160 232 219 / var(--tw-border-opacity)) !important;
    padding: .75rem 1rem !important;
}

.errorshow {
    width: 100%;
    border: 1px solid #e5e7eb;
    border-radius: .375rem;
    font-size: .875rem;
    padding: .5rem 1rem;
    line-height: 21px;
    --tw-text-opacity: 1;
    color: rgb(239 68 68 / var(--tw-text-opacity));
    --tw-bg-opacity: 1;
    background-color: rgb(254 242 242 / var(--tw-bg-opacity));
    --tw-border-opacity: 1;
    border-color: rgb(254 202 202 / var(--tw-border-opacity)) !important;
    padding: .75rem 1rem !important;
    margin-bottom: .75rem;
    margin-top: .75rem;
}

.container {
    padding: 3rem 2.5rem;
    background-color: #fff;
    --tw-ring-offset-shadow: 0 0 #0000;
    --tw-ring-shadow: 0 0 #0000;
    --tw-shadow-color: #f1f5f9;
    --tw-shadow-colored: 0 10px 15px -3px var(--tw-shadow-color), 0 4px 6px -4px var(--tw-shadow-color);
    --tw-shadow: var(--tw-shadow-colored);
    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
    border-radius: 0.5rem;
    width: 100%;
    max-width: 1200px;
    box-sizing: border-box;
    max-height: 700px;
}

.mainCard {
    padding: 2rem;
    border-radius: 0.5rem;
    max-width: 1000px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #ddd;
    overflow-x: auto;
    margin: 0;
}

h4 {
    text-align: center;
    font-size: 20px !important;
    font-weight: bolder;
    margin-bottom: 40px;
}

.registrationContainer h4 {
    font-size: 22px;
    font-weight: bold;
    color: #262262;
    margin: 0px 0px 4px;
}

.registrationContainer {
    max-width: 1200px;
    width: 100%;
    justify-content: center;
    margin-bottom: 2rem;
    align-items: center;
    display: flex;
    flex-direction: column;
}

.formContainer {
    width: 100%;
}

.logoContainer {
    max-width: 300px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.formGroup {
    margin-bottom: 0.75rem;
    /* Updated margin-bottom */
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
}

.formGroup:first-of-type {
    margin-top: 20px;
}

.formGroup label {
    display: block;
    font-weight: normal;
    margin-bottom: 0.25rem;
    font-size: 0.875rem;
    line-height: 1;
    padding-top: 0.1rem;
    padding-bottom: 0.1rem;
}

.formGroup input,
.formGroup textarea,
.formGroup input[type="file"] {
    width: 100%;
    padding: 0.8rem;
    border: 1px solid rgb(226, 232, 240);
    background-color: #fff;
    border-width: 1px;
    border-radius: 0.375rem;
    box-sizing: border-box;
}

.formGroup input:focus,
.formGroup textarea:focus {
    outline: none;
    border: 1px solid #2d0c96;
}

.buttonContainer {
    display: flex;
    justify-content: center;
    margin-top: 1.75rem;
    width: 100%;
}

.buttoncls[type="submit"] {
    background-color: #262262;
    color: #fff;
    border: none;
    padding: 0.8rem 1.5rem;
    border-radius: 0.375rem;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
    z-index: 1;
    width: 100%;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
}

.buttoncls[type="submit"]:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 0;
    height: 0;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 50%;
    z-index: -1;
    transition: all 0.5s ease;
    transform: translate(-50%, -50%);
}

.buttoncls[type="submit"]:hover {
    background-color: #2d0c96;
}

.buttoncls[type="submit"]:hover:before {
    width: 200%;
    height: 200%;
}

.buttoncls[type="submit"]:active {
    transform: scale(0.95);
}

@media (max-width: 768px) {
    .mainCard {
        flex-direction: column;
        align-items: center;
    }

    .backgroundContainer {
        width: 100%;
    }

    .registrationContainer,
    .logoContainer {
        max-width: none;
        margin-left: 0;
        margin-bottom: 2rem;
    }
}

.logo {
    max-width: 60%;
    max-height: 32px;
    margin-right: 0.4rem;
}

.logoHeader {
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
}

.companyName {
    font-size: 20px;
    font-family: 'Public Sans', sans-serif;
    font-weight: bolder;
}

.formTitle {
    font-size: 1.25rem;
    font-weight: 600;
}

.error {
    color: red;
    font-size: 12px;
    margin-top: 0.25rem;
}

.formContainer .formGroup:first-of-type {
    margin-top: 20px;
}

.invalid {
    font-family: 'Times New Roman', Times, serif;
    font-style: normal;
    color: #2d0c96;
    font-weight: 100;
}
