.card {
    display: flex;
    justify-content: center;
    width: 500px;
    background-color: rgb(255, 255, 255);
    --tw-ring-offset-shadow: 0 0 #0000;
    --tw-ring-shadow: 0 0 #0000;
    --tw-shadow-color: #f1f5f9;
    --tw-shadow-colored: 0 10px 15px -3px var(--tw-shadow-color), 0 4px 6px -4px var(--tw-shadow-color);
    --tw-shadow: var(--tw-shadow-colored);
    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  }
  
  .card img {
    width: 3rem;
    height: 2.5rem;
    object-fit: contain;
  }
  
  .section {
    width: 100%;
    padding: 3rem 2.5rem;
    background-color: rgb(255, 255, 255);
  }
  
  .header {
    display: inline-flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    gap: .3rem;
  }
  
  
  .headingsection {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 1.5rem;
    gap: 1rem;
    color: #262262;;
  }
  .section p {
    margin-top: 2rem;
    text-align: center;
    color: rgb(100, 116, 139);
  }
  .headingsection p {
    margin-top: 0;
  }
  
  .loginsection {
    margin-top: 2.5rem;
  }
  
  .inputGroup, .success, .failure {
    margin-bottom: .75rem;
  }
  .success {
    --tw-text-opacity: 1;
    color: rgb(36 151 130 / var(--tw-text-opacity));
    --tw-bg-opacity: 1;
    background-color: rgb(234 250 247 / var(--tw-bg-opacity));
    --tw-border-opacity: 1;
    border-color: rgb(160 232 219 / var(--tw-border-opacity)) !important;
    padding: .75rem 1rem !important;
  }
  .failure {
    --tw-text-opacity: 1;
    color: rgb(239 68 68 / var(--tw-text-opacity));
    --tw-bg-opacity: 1;
    background-color: rgb(254 242 242 / var(--tw-bg-opacity));
    --tw-border-opacity: 1;
    border-color: rgb(254 202 202 / var(--tw-border-opacity)) !important;
    padding: .75rem 1rem !important;
  }
  
  .label {
    display: block;
    margin-bottom: .5rem;
    font-weight: 500;
    font-size: .875rem;
  }
  
  .section input, .success, .failure {
    width: 100%;
    border: 1px solid #e5e7eb;
    border-radius: .375rem;
    font-size: .875rem;
    padding: .5rem 1rem;
    line-height: 21px;
  }
  
  .section input:focus {
    outline: 2px solid #0000;
    outline-offset: 2px;
    border-color: #262262;
  }

  
  .resetbtn {
    margin-top: 2.5rem;
    width: 100%;
    padding: 10px;
    background-color: #262262;
    color: rgb(255, 255, 255);
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  .loginbtn {
    margin-top: 2rem;
    width: 100%;
    padding: 10px;
    background-color: #262262;
    color: rgb(255, 255, 255);
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .loginbtn:hover {
    background-color: #2f2a79;
  }
  
  .error {
    font-size: .8125rem !important;
    --tw-text-opacity: 1;
    color: rgb(239 68 68 / var(--tw-text-opacity)) !important;
    font-size: .875rem !important;
    margin-top: .25rem !important;
    text-align: left !important;
  }
  
  @media (max-width: 600px) {
    .card {
      height: auto;
      margin: 0 15px;
    }
  
    .section {
      width: 100%;
    }
  
    .section input {
      width: 100%;
    }
  
    h2 {
      font-size: medium;
    }
  }
  